<template>
    <div class="order">
        <cube-scroll>
        <ul class="p-list">
            <li class="item" v-for="(x, index) in shopCartList" :key="index">
                <div class="p-img">
                    <img src="https://gd3.alicdn.com/imgextra/i3/2239643951/O1CN01ywTFBz1f3bNyfkZE9_!!2239643951.jpg_400x400.jpg" alt="">
                </div>
                <div class="p-info">
                    <div class="top">
                        <span class="name">{{x.itemName}}</span>
                        <span class="tip">{{x.goodsTexture}}</span>
                    </div>
                    <div class="bottom">¥ {{x.itemPrice}} x {{x.num}}</div>
                </div>
                <div class="total">
                    合计 ￥ {{x.countPrice}}
                </div>
            </li>
            <!-- <li class="item">
                <div class="p-img">
                    <img src="https://gd3.alicdn.com/imgextra/i3/2239643951/O1CN01ywTFBz1f3bNyfkZE9_!!2239643951.jpg_400x400.jpg" alt="">
                </div>
                <div class="p-info">
                    <div class="top">
                        <span class="name">夏装上衣</span>
                        <span class="tip">纯棉</span>
                    </div>
                    <div class="bottom">¥ 100 x 1</div>
                </div>
                <div class="total">
                    合计 ￥ 100
                </div>
            </li> -->
        </ul>
        <div class="totals">
            <div class="text">共计{{numTotal}}件商品</div>
            <div class="num">合计 <span class="price">￥ {{priceTotal}}</span></div>
        </div>
        <ul class="o-list">
            <li class="item">
                <div class="text">预约取件时间</div>
                <div class="chose" @click="showTimePicker">{{time}} <span> > </span></div>
            </li>
            <li class="item">
                <div class="text">默认取货地址</div>
                <div class="chose address" @click="showAddressPicker">
                    <p>{{address}}</p>
                </div>
                <div> > </div>
            </li>
            <li class="item">
                <div class="text">付款方式</div>
                <div class="chose" @click="showPayPicker">{{payWay}} <span> > </span></div>
            </li>
        </ul>
        <div class="remarks">
            <div class="remarks-info">备注信息</div>
            <div class="input">
                <input type="text" placeholder="请输入" @blur="_blur">
            </div>
        </div>
        </cube-scroll>
        <div class="order-btn">确定</div>
    </div>
</template>
<script>
const payWays = [
    {
        text: '微信',
        value: 'weChat',
    },
    {
        text: '支付宝',
        value: 'ali',
    }
]
import { queryShopCart } from "@/api/cart.js";
import { queryDefaultReceiveArea, queryReceiveArea } from '@/api/address.js';
export default {
    name: 'order',
    data() {
        return {
            shopCartList: [],
            time: '请选择',
            address: '',
            payWay: '请选择',
            numTotal: 0,
            priceTotal: 0,
            addressAll: [],
        };
    },
    created() {
        this._queryShopCart();
        this._queryDefaultReceiveArea();
        this._queryReceiveArea();
    },
    methods: {
        // 订单列表
        _queryShopCart() {
            let params = {
                userId: 1,
            }
            queryShopCart(params).then(res => {
                this.shopCartList = res.ShopCartList;
                this.numTotal = res.numTotal;
                this.priceTotal = res.priceTotal;
            }).catch(err => {
            })
        },
        // 默认地址
        _queryDefaultReceiveArea() {
            let params = {
                userId: 1,
            }
            queryDefaultReceiveArea(params).then(res => {
                this.address = `${res.province} ${res.city} ${res.area} ${res.address}`;
            }).catch(err => {
            })
        },
        // 所有地址
        _queryReceiveArea() {
            let params = {
                userId: 1,
            }
            queryReceiveArea(params).then(res => {
                res.forEach(item => {
                    this.addressAll.push({
                        text: `${item.province} ${item.city} ${item.area} ${item.address}`,
                        value: item.id,
                    })
                })
            })
        },
        _blur() {
            setTimeout(function () {
                let scrollHeight = document.documentElement.scrollTop || document.body.scrollTop || 0;
                window.scrollTo(0, Math.max(scrollHeight - 1, 0));
            }, 100);
        },
        // 时间选择
        showTimePicker() {
            this.$createTimePicker({
                showNow: true,
                minuteStep: 5,
                delay: 15,
                onSelect: (selectedTime, selectedText, formatedTime) => {
                    this.time = formatedTime;
                },
                onCancel: () => {
                }
            }).show()
        },
        // 付款方式
        showPayPicker() {
            if (!this.pickerPay) {
                this.pickerPay = this.$createPicker({
                title: '付款方式',
                data: [payWays],
                onSelect: (selectedVal, selectedIndex, selectedText) => {
                   this.payWay = selectedText[0];
                },
                onCancel: this.cancelHandle
                })
            }
            this.pickerPay.show()
        },
        // 选择地点
        showAddressPicker() {
            if (!this.picker) {
                this.picker = this.$createPicker({
                title: '地址选择',
                data: [this.addressAll],
                onSelect: (selectedVal, selectedIndex, selectedText) => {
                    this.address = selectedText[0];
                },
                onCancel: this.cancelHandle
                })
            }
            this.picker.show()
        },
        cancelHandle() {},
    }
}
</script>
<style lang="stylus" scoped>
.order {
    height: calc(100vh - 98px);
    font-family:HiraginoSansGB-W3;
    .p-list {
        padding: 0 30px;
        background-color: #ffffff;
        border-bottom: 0.5px solid #F2F2F2;
        .item {
            display: flex;
            padding: 20px 0;
            border-bottom: 0.5px solid #F2F2F2;
            .p-img {
                width:180px;
                height:120px;
                border-radius:8px;
                img {
                    width:180px;
                    height:120px;
                    border-radius:8px;
                }
            }  
            .p-info {
                margin-left: 20px;
                align-self: flex-end;
                margin-bottom: 15px;
               .top {
                   .name {
                        font-size: 28px;
                        color: #333333;
                    }
                    .tip {
                        font-size: 18px;
                        line-height: 24px;
                        color: #ffffff;    
                        padding: 0 6px;
                        background-color: #F89771;
                        border-radius:8px;
                    }
               }
               .bottom {
                    margin-top: 38px;
                    font-size: 24px;
                    color: #666666; 
                }  
            }
            .total {
                flex: auto;
                align-self: flex-end;
                margin-bottom: 15px;
                text-align: right;
                font-size: 24px;
                font-weight: bold;
                color: #333333;
            }
        }
    }   
    .totals {
        height: 88px;
        padding: 0 30px;
        background-color: #ffffff;
        display: flex;
        justify-content: space-between;
        align-items: center;
        font-size: 24px;
        font-weight: bold;
        color: #333333;
        .num {
            .price {
                color: #F43030;
            }    
        }
    }
    .o-list {
        margin-top: 20px;    
        padding: 0 30px;
        background-color: #ffffff;
        .item {
            display: flex;    
            align-self: center;
            justify-content: space-between;
            padding: 30px 0;
            border-bottom: 0.5px solid #F2F2F2;
            .text {
                font-size: 28px;
                color: #333333;
            }
            .chose {
                font-size: 28px;
                color: #cccccc;
                p {
                    text-align: left;
                    margin-bottom: 12px;
                }
                span {
                    color: #333333;
                }
            }
            .address {
                flex: 1;
                padding: 0 12px;
            }
            .detail-address {
                margin-left: 46px;
                flex: auto;
            }
        }
    }
    .remarks {
        margin-top: 20px;
        display: flex;    
        align-self: center;
        justify-content: space-between;
        padding: 30px;
        background-color: #ffffff;
        .remarks-info {
            font-size: 28px;
            color: #333333;
        }
        .input {
            flex: auto;    
            font-size: 28px;
            color: #CCCCCC;
            text-align: right;
            input {
                padding-left: 30px;
                width: 100%;
            }
        }
    }
    .order-btn {
        position: fixed;
        left: 0;
        bottom: 0;
        // bottom: 82px;
        // left: 75px;
        // margin: 24px auto; 
        // width: 600px;
        width: 100%;
        height: 98px;
        line-height: 98px;
        text-align: center;
        background: #4A4C59;
        // border-radius: 10px;
        font-size: 28px;
        font-weight: bold;
        color: #ffffff;
    }
}
</style>



